import * as React from 'react';
import { graphql } from 'gatsby';
import { GatsbyImage, getImage, IGatsbyImageData } from 'gatsby-plugin-image';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid2';
import Typography from '@mui/material/Typography';
import { Layout } from '../components/layout';
import { Seo } from '../components/seo';
import { Navigation } from '../components/navigation';

interface IProps {
  data: any;
}

const Page = ({ data }: IProps) => {
  const imageData: IGatsbyImageData | undefined = getImage(data.file);

  return (
    <Layout>
      <Navigation />
      <Box py={16}>
        <Grid
          container
          justifyContent='start'
          spacing={{
            md: 8,
            sm: 8,
            xs: 4
          }}
        >
          <Grid
            size={{
              sm: 6,
              xs: 12
            }}
          >
            <Box
              sx={{
                width: {
                  md: '75%',
                  sm: '100%',
                  xs: '100%'
                },
                mx: 'auto',
                pointerEvents: 'none',
                userSelect: 'none',
                // copy css
                height: '100%',
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center'
              }}
              onClick={e => e.preventDefault()}
              onMouseDown={e => e.preventDefault()}
              onMouseUp={e => e.preventDefault()}
              onContextMenu={e => e.preventDefault()}
            >
              {imageData && (
                <GatsbyImage
                  image={imageData}
                  alt='Yuhei Ono'
                  style={{
                    pointerEvents: 'none',
                    userSelect: 'none'
                  }}
                />
              )}
            </Box>
          </Grid>
          <Grid size={{ sm: 6, xs: 12 }}>
            <Box
              display='flex'
              flexDirection='column'
              justifyContent='center'
              gap={4}
              sx={{
                height: '100%'
              }}
            >
              <Box>
                <Typography
                  variant='h5'
                  sx={{
                    letterSpacing: 1.5
                  }}
                >
                  小野 悠平
                </Typography>
                <Typography
                  sx={{
                    letterSpacing: 2
                  }}
                >
                  フォトグラファー
                  <br />
                  ビデオグラファー
                </Typography>
                <Typography mt={2} sx={{ lineHeight: 1.65 }}>
                  1990年 兵庫県神戸市生まれ
                  <br />
                  ビジュアルアーツ専門学校大阪 写真学科 卒業
                  <br />
                  株式会社 博報堂プロダクツ入社を経て
                  <br />
                  インテリアメーカーにて商品撮影に従事
                  <br />
                  2023年 大阪を拠点にフリーランスとして独立
                  <br />
                  ホテル写真,動画撮影を中心に関西で活動中
                </Typography>
              </Box>
              <Box>
                <Typography
                  sx={{
                    letterSpacing: -0.5
                  }}
                >
                  Yuhei Ono | Photographer - Videographer
                </Typography>
                <Typography mt={2} sx={{ lineHeight: 1.65 }}>
                  Born in 1990 in Kobe City, Hyogo Prefecture
                  <br />
                  Graduated from a college in the Department of Photography
                  <br />
                  After working at Hakuhodo Product's Inc.,
                  <br />
                  engaged in product photography at an interior design company
                  <br />
                  Became a freelance photographer in 2023, based in Osaka
                  <br />
                  Currently active in the Kansai region,
                  <br />
                  specializing in hotel photography and videography
                </Typography>
              </Box>
            </Box>
          </Grid>
          <Grid size={12}>
            <Box
              display='flex'
              flexDirection='column'
              alignItems='center'
              justifyContent='start'
            >
              <Box
                display='flex'
                flexDirection='column'
                gap={4}
                sx={{
                  width: {
                    md: 640,
                    sm: 480,
                    xs: 320
                  }
                }}
              >
                <Box>
                  <Typography>来歴</Typography>
                  <Typography fontSize={12} mt={1}>
                    大阪,京都,兵庫など関西を拠点に全国で活動するフォトグラファー兼映像クリエイター。下積み時は博報堂プロダクツで広告カメラマンに師事し、スタジオ・ロケ・物撮り・人物撮影まで多様な現場を経験。退社後は、インテリアメーカーにて商品撮影や動画制作を5年間勤め、独立へ。
                    <br />
                    インテリア撮影で培ったスキルを活かし、独立後はホテル撮影・旅館撮影を主軸に活動。建築撮影、ライフスタイル撮影、フード撮影、プロモーション映像制作まで、宿泊施設の幅広い撮影ニーズにワンストップで対応している。
                    <br />
                    実績が認められ、日本フードフォトグラファー協会に加盟。ホテル撮影カメラマンとして活動しながら飲食店の料理撮影、ECサイトの商品撮影など活動の幅を広げている。広告撮影はもちろん、SNS映えするインスタグラム用の撮影やショート動画制作にも力を入れ、時代が求めるクリエイティブを提供している。
                  </Typography>
                </Box>
                <Box>
                  <Typography>受賞歴</Typography>
                  <Typography fontSize={12} mt={1}>
                    2024年02月 FPA Professional Food Photography Award 2023
                    SILVER AWARD 受賞
                    <br />
                    2024年02月 FPA Professional Food Photography Award 2023
                    BROZE AWARD 受賞
                    <br />
                    <small>
                      （FPA AWARDS:
                      日本フードフォトグラファー協会主催の日本最大のフードフォトコンテスト）
                    </small>
                  </Typography>
                </Box>
                <Box>
                  <Typography>出展歴</Typography>
                  <Typography fontSize={12} mt={1}>
                    2017年07月 VACUUM
                    GALLERYにてストリートスナップ写真の個展『幻影の街』を開催
                    <br />
                    2024年11月 東京・渋谷ヒカリエ 8/ CUBEにて『FPA Professional
                    Food Photography Award Exhibition 2024』に受賞作品を出展
                  </Typography>
                </Box>
              </Box>
            </Box>
          </Grid>
        </Grid>
      </Box>
    </Layout>
  );
};

export const query = graphql`
  {
    file(relativePath: { eq: "profile.jpg" }) {
      childImageSharp {
        gatsbyImageData(width: 1200)
      }
    }
  }
`;

export default Page;

export const Head = () => {
  return <Seo title='PROFILE' />;
};
